body {
  margin: 155px 0 0 0;
  padding: 2px;
  padding-top: 2px;
  font-family: Verdana;
  color: black;
  background-color: rgb(220, 236, 77);
  background-image: url("1878518.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center;
  text-decoration-color: aliceblue;
}

body h1 {
  color: aliceblue;
}

.btn {
  border-radius: 40px;
}

.btn--primary {
  background-color: yellow;
  color: rgb(233, 220, 34);
}

.logo-container {
  position: absolute;
  top: 0;
  right: 2;
}

.logo-container::after {
  content: url(aedislogo.png);
  display: block;
  width: 100px;
  height: 100px;
}
